// 促销手段中的产品树
import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { TreeNode } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun'
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { ProductManageHttpService } from '../../../../product-manage/http/product-manage.http.service';
import { EnterpriseDirectoryBean } from '../../../../product-manage/product-summary/enterprise-directory/bean/enterprise-directory.bean';
@Component({
	selector: 'marketing-product-tree',
	templateUrl: './marketing-product-tree.component.html',
	styleUrls: ['./marketing-product-tree.component.scss'],
	providers: [ProductManageHttpService, Commfunc, CustomeHttpService]
})
export class MarketingProductTree implements OnInit {
	@Input() inValue: any;
	@Output() outValue: EventEmitter<any> = new EventEmitter();
	selectedFiles: TreeNode[];
	first: number = 0;//分页控制
	arrangValue: string;//展示层级
	//总页数
	total: string;
	//每页多少条
	pageSize: number = 10;
	//每页多少条
	pageNum: number = 1;
	lineID: string;//暂存线ID
	groupID: string;//暂存组ID
	baseID: string;//暂存基础ID
	//提示信息
	msgs: Message[] = [];
	//列表
	cars_2: any;
	treeData: any;
	listData: any = {};
	lineValue: any = {};
	groupValue: any = {};
	valueProduct: any = {
		productLineId: '',//产品线ID
		productGroupId: '',//产品组id
		productTemplateId: '',//基础产品id
		pageSize: 10,
		pageNum: 1,
	}
	//模态框
	showModel: string;
	headerTitle: string
	display: boolean = false;

	//组件的参数
	updateValue: any = [];
	globalDirectoryBean: EnterpriseDirectoryBean = new EnterpriseDirectoryBean();
	//接受选中的值
	checkListPro: any = [];
	parent_info: string;
	flag: string;
	showFlag: string;
	selectedCities: any = [];
	prdoctIdList:any=[];
	productId:string;
	productArr=[];
	constructor(
		public productPlatformHttpService: ProductManageHttpService,
		public commfunc: Commfunc,
		public customeHttpService: CustomeHttpService,
		public activatedRoute: ActivatedRoute,
	) { }

	ngOnInit() {
		setTimeout(()=>{
			this.cars_2=[]
		})
		this.productArr = this.inValue.productList;
		this.query_line();
		this.flag = this.inValue.flag;
		if (this.flag == '1') {
			this.showFlag = '1';//是1的话显示单选框
		} else if (this.flag == '2') {
			this.showFlag = '2';//否则显示多选框
		}
		if (this.inValue.productList) {
			this.inValue.productList.forEach(element => {
				if (element.productId!='') {
					this.productId=element.productId;
					this.prdoctIdList.push(this.productId)
				}
			});
		}
	}

	// 详情
	basicDetails(item) {
		this.display = true;
		this.headerTitle = '可售产品详情';
		this.showModel = '1';
		this.updateValue = item.productId;
	}
	detailsCall(param) {
		this.display = param;
	}
	query_line() {
		this.productPlatformHttpService.productCategoryLineQueryList(this.globalDirectoryBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				let tempArr = [];
				data.productLineList.forEach(item => {
					let tempObj = { id: 'line' };
					tempObj['label'] = item['productLineName'];
					tempObj['value'] = item;
					tempObj['children'] = [{}];
					tempArr.push(tempObj)
				});
				this.treeData = tempArr;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}
	//基础查可售
	baseQuery(data) {
		let arr = [];
		this.productPlatformHttpService.findAvailableProductList(data).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.cars_2 = data.resultList;
				this.total = data.total;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}
	//点击层级
	nodeCheck(event) {
		if (event.node.id == "line") {
			// this.arrangValue = "line";
			this.lineValue = event.node.value;
			this.lineID = event.node.value.productLineId;
			this.cars_2 = [];
			this.pageNum = 1;
			this.pageSize = 10;
			this.valueProduct = {
				productLineId: this.lineID,//产品线ID
				productGroupId: '',//产品组id
				productTemplateId: '',//基础产品id
				pageSize: this.pageSize,
				pageNum: this.pageNum,
			}
			this.first = 0;
			this.baseQuery(this.valueProduct);
		}
		if (event.node.id == "group") {
			console.log(event.node.value)
			// this.arrangValue = "group";
			this.groupValue = event.node.value;
			this.groupID = event.node.value.groupId;
			this.lineID = event.node.value.productLineId;
			this.cars_2 = [];
			this.pageNum = 1;
			this.pageSize = 10;
			this.valueProduct = {
				productLineId: this.lineID,//产品线ID
				productGroupId: this.groupID,//产品组id
				productTemplateId: '',//基础产品id
				pageSize: this.pageSize,
				pageNum: this.pageNum,
			}
			this.first = 0;
			this.baseQuery(this.valueProduct);
		}
		if (event.node.id == "jichu") {
			this.cars_2 = [];
			// this.arrangValue = "jichu";
			this.baseID = event.node.value.tplId;
			this.pageNum = 1;
			this.pageSize = 10;
			this.valueProduct = {
				productLineId: '',//产品线ID
				productGroupId: '',//产品组id
				productTemplateId: this.baseID,//基础产品id
				pageSize: this.pageSize,
				pageNum: this.pageNum,
			}
			this.first = 0;
			this.baseQuery(this.valueProduct);
			let params = {
				productId: event.node.value.tplId
			}
			this.productPlatformHttpService.getBaseProduct(params).subscribe(data => {
				if (data.returnCode.code == "success") {
					this.listData = data.resultList[0];
				} else {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
				}
			}, error => {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
			})
		}
	}
	//+号点击
	nodeExpend(event) {
		if (event.node.id == "line") {
			this.globalDirectoryBean.productLineId = event.node.value.productLineId;
			let arr = [];
			this.productPlatformHttpService.productCategoryGroupInfoQueryList(this.globalDirectoryBean).subscribe(data => {
				if (data.returnCode.code == "success") {
					data.productGroupInfoList.forEach(item => {
						let tempObj = { id: 'group' };
						tempObj['label'] = item['groupName'];
						tempObj['value'] = item;
						tempObj['children'] = [{}];
						arr.push(tempObj)
					});
					event.node.children = arr
				} else {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
				}
			}, error => {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
			})
		}
		if (event.node.id == "group") {
			let param = {
				groupId: event.node.value.groupId
			}
			let arr = [];
			this.productPlatformHttpService.pdGroupTemplateQuery(param).subscribe(data => {
				if (data.returnCode.code == "success") {
					data.groupTempList.forEach(item => {
						let tempObj = { id: 'jichu' };
						tempObj['label'] = item['tplName'];
						tempObj['value'] = item;
						tempObj['children'] = [{}];
						arr.push(tempObj)
					});
					event.node.children = arr
				} else {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
				}
			}, error => {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
			})
		}
		if (event.node.id == "jichu") {
			let arr = [];
			event.node.children = arr
		}
	}

	//将勾选的列表放到对象中(多选)
	checkClick(item,ri, event) {
		console.log(this.prdoctIdList)
		this.checkListPro = [];
		for(let i of this.cars_2) {
			for(let j of this.prdoctIdList)
			if(i.productId==j){
				this.checkListPro.push(i)
			}
		}
		if(event==true) {
			this.productArr.push(item);
			console.log(this.productArr)
		}else{
			for(let i in this.productArr){
				if(this.productArr[i].productId == item.productId){
					this.productArr.splice(parseInt(i),1)
				}
			}
		}
	}
	//单选
	checkClick2(item, event) {
		this.checkListPro = [item]
	}

	//点击确定
	addBtnCom() {
		if (this.flag == '1') {
			this.outValue.emit(this.checkListPro)
		}else if (this.flag == '2') {
			this.outValue.emit(this.productArr)
		}
	}
	//取消
	quxiao () {
		this.outValue.emit(false)
	}
	//分页
	paginate(event) {
		//每页显示的条数
		this.pageSize = event.rows;
		//当前页
		let currentPage = event.page + 1;
		this.pageNum = currentPage;
		//调用查询的方法
		this.first = (currentPage - 1) * this.pageSize;
		this.valueProduct.pageSize = this.pageSize;
		this.valueProduct.pageNum = this.pageNum;
		this.baseQuery(this.valueProduct);
		this.pageNum = 1;
	}

}
